import { useLocation, useParams } from "react-router-dom";
import { assestsUserLmsUrl } from "../../Services/UrlConfig/AssestsBaseUrl";
import UserMemberTabsWrapper from "../../Ui/UserMember/UserMemberTabsWrapper";
import SquareTabWrapper from "../../Ui/Tab/Square/SquareTabWrapper";
import SquareTabCard from "../../Ui/Tab/Square/SquareTabCard";
const LmsModeratorProfile = () => {
  const params = useParams();
  const getQuery = useLocation();
  let measure = document.getElementById("799nxhgh")?.clientWidth;
  let height = "9rem";
  let width = "9rem";
  if (measure) {
    let disvible =
      measure > 870 ? (measure > 1100 ? 6 : 5) : measure > 700 ? 4 : 3;
    let mi = measure > 870 ? 1 : 2;
    width = `${Math.floor((measure - mi - 31 * disvible - 1) / disvible)}px`;
    height = width;
  }
  return (
    <UserMemberTabsWrapper>
      <SquareTabWrapper dynamicId="799nxhgh">
        {getQuery.state?.role === "LEAVE_SANCTION_ACCESS" ? null : (
          <SquareTabCard
            label="all_staff"
            iconUrl={`${assestsUserLmsUrl}/staff.svg`}
            url={`/q/${params.username}/member/staff/lms?type=mod-staff`}
            state={getQuery.state}
            isAuthorized
            isVisible={{ all_staff: true }}
            accessKey="all_staff"
            customStyleContainer={{
              height: height,
              width: width,
            }}
          />
        )}

        <SquareTabCard
          label="leave_request"
          iconUrl={`${assestsUserLmsUrl}/leave-request.svg`}
          url={`/q/${params.username}/member/staff/lms?type=mod-request`}
          state={getQuery.state}
          isAuthorized
          isVisible={{ leave_request: true }}
          accessKey="leave_request"
          customStyleContainer={{
            height: height,
            width: width,
          }}
        />
        <SquareTabCard
          label="leave_history"
          iconUrl={`${assestsUserLmsUrl}/issue.svg`}
          url={`/q/${params.username}/member/staff/lms?type=mod-history`}
          state={getQuery.state}
          isAuthorized
          isVisible={{ leave_history: true }}
          accessKey="leave_history"
          customStyleContainer={{
            height: height,
            width: width,
          }}
        />
      </SquareTabWrapper>
    </UserMemberTabsWrapper>
  );
};

export default LmsModeratorProfile;
